<template>
  <v-container>    
    <v-layout row wrap >
      <v-flex xs12 md8 offset-md2 class="mt-3">
          <!-- errorfalsetrueAlert -->
        <v-layout row v-if="error" >
          <v-flex sm10 offset-sm1>
            <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
          </v-flex>
        </v-layout>    
        <v-layout row>
          <v-flex sm10 offset-sm1>
            <v-card>
              <v-card-text>
                <v-container>
                  <form @submit.prevent="onSignin">
                    <h2>Sign In </h2>
                    <v-layout row>
                      <v-flex xs12>
                        <v-text-field
                        name="email"
                        label="Mail"
                        id="signInEmail"
                        v-model="signInEmail"
                        type="email"
                        required
                        >
                        </v-text-field>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex xs12>
                        <v-text-field
                        name="password"
                        label="Password"
                        id="signInPassword"
                        v-model="signInPassword"
                        type="password"
                        required
                        >
                        </v-text-field>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex xs12>
                        <v-btn type="submit" :disabled="loading||!SigninData" :loading="loading">Sign in
                          <span slot="loader" class="custom-loader">
                          <v-icon light>cached

                          </v-icon>
                          </span>
                        </v-btn>
                      </v-flex>
                    </v-layout>
                  </form>
                </v-container>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>


  </v-container>

</template>

<script>
  export default{
    data (){
      return {
        signInEmail: '', /*  */
        signInPassword: '', /*  */
      }
    },
    computed: {
      /*  */
      SigninData(){
          return this.signInPassword !== ''&& this.signInEmail !== ''
      },

      /* usernullstore mutation "setUser"commit *
       *  "newUser"watchuser  */
      user (){
        return this.$store.getters.user
      },

      /* errornull(false)store mutation "setError"commit *
       *  firebase  "error"error!null(true)Alert */
      error(){
        return this.$store.getters.error
      },

      /* loadingfalsestore mutation "setLoading" commit *
       * loadingloadingtrue */
      loading(){
        return this.$store.getters.loading
      },


    },
    watch:{
      /* usernullundefined */
      user (value){
        if(value !== null && value !== undefined){
          this.$router.push('/')
        }
      }
    },

    methods:{
      /* store signUserIn */
      onSignin (){
        this.$store.dispatch('signUserIn',{email: this.signInEmail, password: this.signInPassword})

      },

      /* click Alert'X'btn(@dismissed)onDismissed  store "clearError" */
      onDismissed() {
        // console.log('Dismissed Alert!')
        this.$store.dispatch('clearError')
      }
    }
  }
</script> 


