<template>
    <v-dialog width="500px" persistent v-model="editDialog">
        <v-btn small flat  accent slot="activator" @click="dataprint">
            
            <v-icon >edit</v-icon>
        </v-btn>
        
        <!--   (user.id)(creatorId) -->
        <v-card v-if="userIsCreator">
            <v-container row wrap>
                <v-layout>                    
                    <v-flex xs12>
                        <v-card-title>Edit Work</v-card-title>
                    </v-flex>
                </v-layout>
                <v-divider></v-divider>
                <v-layout>
                    <v-flex xs12  >
                        <v-card-text >
                            <v-text-field
                                name="title"
                                label=" Title"
                                id="title"
                                v-model="editedtitle"
                                required>
                            </v-text-field>

                            <!--  -->
                            <v-select
                                :items="types"
                                v-model="value"
                                label=" WorkType"
                                data-vv-name="select"
                                required
                            ></v-select>

                            <!--  -->
                            <v-text-field
                                name="content"
                                label=" Content"
                                id="content"
                                v-model="editedcontent"
                            >
                            </v-text-field>

                            <!-- JS -->
                            <v-text-field
                                name="jsframe"
                                label="JS JsFrame"
                                id="jsframe"
                                v-model="editedjsframe"
                            >
                            </v-text-field>

                            <!-- UI -->
                            <v-text-field
                                name="uiframe"
                                label="UI UiFrame"
                                id="uiframe"
                                v-model="editeduiframe"
                            >
                            </v-text-field>
                            
                            <!--  -->
                            <v-btn raised class="secondary" @click="onPickFile"></v-btn>
                            <!-- inputonPickFile -->
                            <input 
                                type="file" 
                                ref="fileInput"
                                style="display:none" 
                                accept="image/*"
                                @change="onFilePicked"
                            >

                            <!--  -->
                            <img :src="editedimgurl" width="100%">

                            <!-- github -->
                            <v-text-field
                                name="githubUrl"
                                label="Github githubUrl"
                                id="githubUrl"
                                v-model="editedgithub"
                                required
                            >
                            </v-text-field>

                            <!--  -->
                            <v-text-field
                                name="viewUrl"
                                label=" viewUrl"
                                id="viewUrl"
                                v-model="editedview"
                                required
                            >
                            </v-text-field>
                        </v-card-text>
                    </v-flex>
                </v-layout>
                <v-divider></v-divider>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn flat class="red--text darken-1" @click="editDialog = false">Close</v-btn>
                            <v-btn flat class="primary darken-1" @click="onSaveChanges">Save</v-btn>
                        </v-card-actions>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-card>
        <!--   End -->
        <!--    -->
        <v-card v-else>
            <v-container row wrap>

                <v-layout>
                    <v-flex xs12  >
                        <h3></h3>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn flat class="red--text darken-1" @click="editDialog = false">Close</v-btn>
                        </v-card-actions>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-card>
        <!--   End  -->

    </v-dialog>
</template>

<script>
    export default {
        props: ['work'],
        data() {
            return {
                userIsCreator: true,
                editDialog: false,
                value: '',  /*  */
                editedtitle: '', /*  */
                editedcontent: '',  /*  */
                editedjsframe: '',  /* js */
                editeduiframe: '',  /* ui */
                editedimgurl: '',  /* Data URL(base64) */
                editedgithub: '',  /* giithub */
                editedview: '',  /*  */
                editedimage: '', /*  */
                types: [  /*  */
                    'Html5+Css',
                    'Javascript',
                    'Vue',
                ],

            }
        },
        computed: {

            /*  */
            // userIsCreator() {
            //     return this.$store.getters.user.id === this.work.CreatorId
            // },

        },
        methods:{
            /*  */
            dataprint(){
                let apple = this.$store.getters.user
                console.log('work', this.work)
                console.log('user', apple)
                this.editedtitle = this.work.Title,
                this.value = this.work.Worktype
                this.editedcontent = this.work.Content
                this.editedjsframe = this.work.Jsframe
                this.editeduiframe = this.work.Uiframe
                this.editedimgurl = this.work.ImgUrl
                this.editedgithub = this.work.Github
                this.editedview = this.work.View
            },

            /*  */
            onPickFile(){
                /* fileInputinputrefinputonPickFile */
                this.$refs.fileInput.click(); 
            },

            /* inputchange */
            onFilePicked(event){
                // console.log('event', event)
                // console.log('files', files)
                const files = event.target.files
                // console.log('files', files)
                let filename = files[0].name
                // console.log('filename', filename)
                /* lastIndexOf()  */
                if (filename.lastIndexOf('.') <= 0){
                    return alert('Please add a valid file!')
                }
                const fileReader = new FileReader()
                // console.log('fileReader',fileReader)

                /* load  */
                fileReader.addEventListener('load', () => {
                    this.editedimgurl = fileReader.result
                })
                
                /* FileReaderreadAsDataURLData URL */
                fileReader.readAsDataURL(files[0])
                // console.log('files[0]',files[0])

                this.editedimage = files[0]
                // console.log('image',this.image)

                // console.log('img',this.img)
            },
            
            /*  */
            onSaveChanges(){
                /* trim() */
                if(this.editedtitle.trim() === '' && 
                this.editedcontent.trim() === '' && this.editedimgurl.trim() === ''){
                    return
                }

                /*  */
                this.editDialog = false

                /*  */
                this.$router.push('/works/all')

                /* store updateWorkData *
                 *  &id  */
                console.log('')
                
                this.$store.dispatch('updateWorkData', {
                    Id: this.work.Id, /* ID */

                    Title: this.editedtitle, /*  */
                    Worktype: this.value,  /*  */
                    Content: this.editedcontent,  /*  */
                    Jsframe: this.editedjsframe,  /* js */
                    Uiframe: this.editeduiframe,  /* ui */
                    ImgUrl: this.editedimgurl,  /* Data URL(base64) */
                    Github: this.editedgithub,  /* giithub */
                    View: this.editedview,  /*  */
                })
            }
        }
    }
</script>


