<template>
    <v-container>
        <v-layout row>
            <v-flex xs12>
                <h2 class="secondary--text"></h2>
            </v-flex>
        </v-layout>
        <v-layout row>
            <v-flex xs12>
                <form @submit.prevent="onAddWork">
                    <v-layout row>
                        <v-flex xs12 sm6 offset-sm3>
                            <!--  -->
                            <v-text-field
                                name="title"
                                label=" Title"
                                id="title"
                                v-model="title"
                                required>
                            </v-text-field>

                            <!--  -->
                            <v-select
                                :items="types"
                                v-model="value"
                                label=" WorkType"
                                data-vv-name="select"
                                required
                            ></v-select>

                            <!--  -->
                            <v-text-field
                                name="content"
                                label=" Content"
                                id="content"
                                v-model="content"
                            >
                            </v-text-field>

                            <!-- JS -->
                            <v-text-field
                                name="jsframe"
                                label="JS JsFrame"
                                id="jsframe"
                                v-model="jsframe"
                            >
                            </v-text-field>

                            <!-- UI -->
                            <v-text-field
                                name="uiframe"
                                label="UI UiFrame"
                                id="uiframe"
                                v-model="uiframe"
                            >
                            </v-text-field>
                            
                            <!--  -->
                            <v-btn raised class="secondary" @click="onPickFile"></v-btn>
                            <!-- inputonPickFile -->
                            <input 
                                type="file" 
                                ref="fileInput"
                                style="display:none" 
                                accept="image/*"
                                @change="onFilePicked"
                            >

                            <!--  -->
                            <img :src="imgurl" width="100%">

                            <!-- github -->
                            <v-text-field
                                name="githubUrl"
                                label="Github githubUrl"
                                id="githubUrl"
                                v-model="github"
                                required
                            >
                            </v-text-field>

                            <!--  -->
                            <v-text-field
                                name="viewUrl"
                                label=" viewUrl"
                                id="viewUrl"
                                v-model="view"
                                required
                            >
                            </v-text-field>

                        </v-flex>
                    </v-layout>

                    <v-layout row>
                        <v-flex xs12 sm6 offset-sm3>
                            <v-btn class="primary" :disabled="!formIsValid" type="submit"></v-btn>
                        </v-flex>
                    </v-layout>
                </form>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    export default{
        data(){
            return{
                title: '', /*  */
                value: null,  /*  */
                content: '',  /*  */
                // subtitle: '',  /* */
                jsframe: '',  /* js */
                uiframe: '',  /* ui */
                imgurl: '',  /* Data URL(base64) */
                github: '',  /* giithub */
                view: '',  /*  */
                image: '', /*  */
                types: [  /*  */
                    'Html5+Css',
                    'Javascript',
                    'Vue'
                ],
            }
        },
        computed:{
            /*  */
            formIsValid(){
                return this.title !== ''&& 
                this.value !== null &&
                this.imgurl !== '' &&
                this.github !== '' &&
                this.view !== ''
            },

        },
        methods:{
            /*  */
            onAddWork(){

                const workData={
                    title: this.title,
                    worktype: this.value,
                    content: this.content,
                    jsframe: this.jsframe,
                    uiframe: this.uiframe,
                    imgurl: this.imgurl,
                    github: this.github,
                    view: this.view,
                    image: this.image
                }

                /* workDataimgurlfiles[0](image)*/


                console.log('workData',workData)
                // console.log('I add work')
                this.$store.dispatch('addToWork', workData)
                this.$router.push('/works/all')

            },

            /*  */
            onPickFile(){
                /* fileInputinputrefinputonPickFile */
                this.$refs.fileInput.click(); 
            },

            /* inputchange */
            onFilePicked(event){
                // console.log('event', event)
                // console.log('files', files)
                const files = event.target.files
                // console.log('files', files)
                let filename = files[0].name
                // console.log('filename', filename)
                /* lastIndexOf()  */
                if (filename.lastIndexOf('.') <= 0){
                    return alert('Please add a valid file!')
                }
                const fileReader = new FileReader()
                // console.log('fileReader',fileReader)

                /* load  */
                fileReader.addEventListener('load', () => {
                    this.imgurl = fileReader.result
                })
                
                /* FileReaderreadAsDataURLData URL */
                fileReader.readAsDataURL(files[0])
                // console.log('files[0]',files[0])

                this.image = files[0]
                // console.log('image',this.image)

                // console.log('img',this.img)
            }
        }
    }
</script>


