<template>
  <v-container>    
    <v-layout row wrap >
      <v-flex xs12 md8 offset-md2 class="mt-3">
        <v-layout row v-if="error">
          <!-- errorfalsetrueAlert -->
          <v-flex sm10 offset-sm1>
            <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex sm10 offset-sm1>
            <v-card>
              <v-card-text>
                <v-container>
                  <form @submit.prevent="onSignup">
                    <h2>Sign Up </h2>
                    <v-layout row>
                      <v-flex xs12>
                        <v-text-field
                        name="email"
                        label="Mail"
                        id="signUpEmail"
                        v-model="signUpEmail"
                        type="email"
                        required
                        >
                        </v-text-field>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex xs12>
                        <v-text-field
                        name="password"
                        label="Password"
                        id="signUpPassword"
                        v-model="signUpPassword"
                        type="password"
                        required
                        > 
                        </v-text-field>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex xs12>
                        <v-text-field
                        name="confirmPassword"
                        label="Confirm Password"
                        id="confirmPassword"
                        v-model="confirmPassword"
                        type="password"
                        :rules="[comparePasswords]"
                        >
                        </v-text-field>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex xs12>
                        <!-- loadingtruebtn"disabled" "loading" -->
                        <!-- dataNotReadytruebtn"disabled" -->
                        <v-btn type="submit" :disabled="loading||!SignupData" :loading="loading">Sign up
                          <span slot="loader" class="custom-loader">
                          <v-icon light>cached

                          </v-icon>
                          </span>
                        </v-btn>

                      </v-flex>
                    </v-layout>
                  </form>
                </v-container>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>


  </v-container>

</template>

<script>
  export default {
    data (){
      return {
        signUpEmail: '', /*  */
        signUpPassword: '', /*  */
        confirmPassword: '' /*  */
      }
    },
    computed: {
      /*  */
      SignupData(){
          return this.signUpPassword == this.confirmPassword && /*  */
          this.signUpPassword !== ''&&
          this.confirmPassword !== ''&&
          this.signUpEmail !== ''
      },

      /* */
      comparePasswords(){
        return this.signUpPassword !== this.confirmPassword ? 'Passwords do not match' : 'ok'
      },

      /* usernullstore mutation "setUser"commit *
       *  "newUser"watchuser  */
      user(){
        return this.$store.getters.user
      },

      /* errornull(false)store mutation "setError"commit *
       *  firebase  "error"error!null(true)Alert */
      error(){
        return this.$store.getters.error
      },

      /* loadingfalsestore mutation "setLoading" commit *
       * loadingloadingtrue */
      loading(){
        return this.$store.getters.loading
      },

    },
    watch:{
      /* usernullundefined */
      user (value){
        if(value !== null && value !== undefined){
          this.$router.push('/')
        }
      },

    },
    methods:{
      /* store signUserUp */

      onSignup(){
        /* clickbtn(@submit)onSignup dispatch store "signUserUp"emailpassword */
        this.$store.dispatch('signUserUp',{email: this.signUpEmail, password: this.signUpPassword})
      },

      /* click Alert'X'btn(@dismissed)onDismissed  store "clearError" */
      onDismissed() {
        this.$store.dispatch('clearError')
      }
    }
  }
</script> 


