<template>

  <v-container fluid grid-list-xs>
    <v-layout row wrap v-if="loading">
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular
          indeterminate
          class="primary--text"
          :width="7"
          :size="70"></v-progress-circular>
      </v-flex>
    </v-layout>
    <!--  Page1-->
    <v-layout row wrap v-else class="workCard">
      <v-flex sm12 md6 offset-md0 v-for="(work, index) in works" :key="index" class="mb-4 text--left pl-3 pr-3">
        <v-card>
          <v-card-title>
            <h2 class="dark--text">{{work.Title}}</h2>
                        <!--  :  -->
            <template v-if="userIsAuthenticated">
              <v-spacer></v-spacer>
              <app-edit-work :work="work"></app-edit-work>
            </template>
            <!--  End -->
          </v-card-title>
          <v-img
            :src="work.ImgUrl"
            height="300px"
          ></v-img>
          <v-card-text class="work-content">
            <v-chip label outline color="primary" v-if="work.Jsframe">{{work.Jsframe}}</v-chip>
            <v-chip label outline color="secondery" v-if="work.Uiframe">{{work.Uiframe}}</v-chip>
            <div class="error--text">{{work.Content}}</div>
            <!-- <div >{{work.Datatype}}</div> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat color="orange" @click="openLink(work.View)"  >VIEW</v-btn>
            <v-btn flat  color="orange" @click="openLink(work.Github)" >GITHUB</v-btn>
          </v-card-actions>
        </v-card>
        

      </v-flex>

    </v-layout>
    <!--  Page1-->
    <v-layout row wrap v-else class="workCard">
      <v-flex sm12 md6 offset-md0 v-for="(work, index) in works" :key="index" class="mb-4 text--left pl-3 pr-3">
        <v-card>
          <v-card-title>
            <h2 class="dark--text">{{work.Title}}</h2>
                        <v-chip label v-if="work.Jsframe">{{work.Jsframe}}</v-chip>
            <v-chip label v-if="work.Cssframe">{{work.Cssframe}}</v-chip>
                        <!--  :  -->
            <template v-if="userIsAuthenticated">
              <v-spacer></v-spacer>
              <app-edit-work :workData="workData"></app-edit-work>
            </template>
            <!--  End -->
          </v-card-title>
          <v-img
            :src="work.ImgUrl"
            height="300px"
          ></v-img>
          <v-card-text class="work-content">

            <div class="error--text">{{work.Content}}</div>
            <!-- <div >{{work.Datatype}}</div> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat color="orange" @click="openLink(work.View)"  >VIEW</v-btn>
            <v-btn flat  color="orange" @click="openLink(work.Github)" >GITHUB</v-btn>
          </v-card-actions>
        </v-card>
        

      </v-flex>
    </v-layout>
    <!--  Page1 End-->

    <!--  Page2-->
    <v-layout class="workDetail" style="display: none; z-index: 1;">
      <v-flex md10 offset-md1>
        <v-card>
          <v-card-title>
            <h2 class="dark--text">{{workData.Title}}</h2>

            <!--  :  -->
            <template v-if="userIsAuthenticated">
              <v-spacer></v-spacer>
              <app-edit-work :workData="workData"></app-edit-work>
            </template>
            <!--  End -->
          </v-card-title>
          <v-img
            :src="workData.ImgUrl"
            height="400px"
          ></v-img>
          <v-card-text>
            <div class="error--text">{{workData.Content}}</div>
            <div>{{workData.Subtitle}}</div>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat color="orange" @click="goBack()">Go Back</v-btn>
            <v-btn flat color="orange" @click="openLink(workData.View)"  >VIEW</v-btn>
            <v-btn flat color="orange" @click="openLink(workData.Github)" >GITHUB</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>      
    </v-layout>
    <!--  Page2 End-->
  </v-container>

</template>

<script>

  export default {
    data() {
      return {
        currentPage: 'Card', /*  */
        workData:{
          Title: '', /*  */
          Worktype: '',  /*  */
          Content: '',  /*  */
          Jsframe: '',  /* js */
          Uiframe: '',  /* ui */
          ImgUrl: '',  /* Data URL(base64) */
          Github: '',  /* giithub */
          View: '',  /*  */
          CreatorId: '', /* ()id */
          Key: '', /* firebaseid */
        }
      }
    },
    computed: {
      /*  */
      works () {
        // console.log('jsworks:loadedWorks', this.$store.getters.loadedWorks)
        return this.$store.getters.loadedWorks  
      },

      /*  */
      userIsAuthenticated() {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
      loading () {
        return this.$store.getters.loading
      }
    },
    methods:{
      /* (.workDetail) */
      goDetail(id) {
        // console.log('id', id)
        // console.log('which', this.works)

        const showDetail = document.querySelector('.workDetail');
        showDetail.style.display =  `block`;

        let next = 'Detail'

        this.showContent(next, id);
      },

      /*  */
      showContent(next, id) {
        const curPage = ".work"+this.currentPage; /* goDetail:.workCard */ /* goBack:.workDetail */
        const nextPage =".work"+next; /* goDetail:.workDetail */ /* goBack:.workCard */
        const curPageClass = document.querySelector(curPage); /* .workCard/.workDetailsection */
        const nextPageClass = document.querySelector(nextPage); /* .workDetail/.workCardsection */

        // console.log('showContent')

        if(this.currentPage == next){
          // console.log('return')
          return;
        }
        if(this.currentPage === 'Card'){
          // console.log('currentPage === "Card"',)
          nextPageClass.classList.add('rightToleft'); /* .workDetail*/
          curPageClass.style.display = 'none' ; /* .workCard */
          curPageClass.style.zIndex = '1' ; /* .workCard zindex  1 */
          nextPageClass.style.zIndex = '2' ; /* .workDetail zindex  2 */

          this.currentPage = 'Detail'; /* Detail */
          // console.log('currentPage  "Detail"')

          setTimeout(function(){
            curPageClass.classList.remove('leftToright');
          }, 500)

          /*  */
          this.workData.Title = this.$store.state.loadedWorks[id].Title;
          this.workData.Worktype = this.$store.state.loadedWorks[id].Worktype;
          this.workData.Content = this.$store.state.loadedWorks[id].Content;
          this.workData.Jsframe = this.$store.state.loadedWorks[id].Jsframe;
          this.workData.Uiframe = this.$store.state.loadedWorks[id].Uiframe;
          this.workData.ImgUrl = this.$store.state.loadedWorks[id].ImgUrl;
          this.workData.Github = this.$store.state.loadedWorks[id].Github;
          this.workData.View = this.$store.state.loadedWorks[id].View;
          this.workData.CreatorId = this.$store.state.loadedWorks[id].CreatorId;
          this.workData.Key = this.$store.state.loadedWorks[id].Id;

          // console.log('workData',this.workData)
        }else{
          // console.log('currentPage  "Card"',)
          /* nextPageClass.workCard */
          nextPageClass.classList.add('leftToright');
          /* nextPageClass.style.display = '' ; */
          curPageClass.style.zIndex = '1' ;  /* .workDetail zindex  1 */
          nextPageClass.style.zIndex = '2' ;  /* .workCard zindex  2 */
          this.currentPage = next; /* Card */

          /*  */
            // console.log('currentPage  "Card"')

          setTimeout(function(){
            curPageClass.classList.remove('rightToleft');
          }, 500)
        }
        
        window.document.documentElement.scrollTop = 0; //
            // console.log('')


      },
      /*  */
      openLink(site){
        window.open(site , '', 'height=1000,width=500');
      },

      /* (.workCard) */
      goBack() {
        const hideDetail = document.querySelector('.workDetail');
        hideDetail.style.display = `none`;

        const showCard = document.querySelector('.workCard');
        showCard.style.display = ``;

        let next = 'Card'

        this.showContent(next);
        // console.log('next', next) 
      },

    }
  }
</script>
<style>
  .work-content{
    height: 70px;
  }
  /*  */
  @-moz-keyframes rightToleft {
	from {
	  transform: translateX(100%);
	}
	to {
	  transform: translateX(0);
	}
  }
  @-webkit-keyframes rightToleft {
	from {
	  transform: translateX(100%);
	}
	to {
	  transform: translateX(0);
	}
  }
  @keyframes rightToleft {
	from {
	  transform: translateX(100%);
	}
	to {
	  transform: translateX(0);
	}
  }

  .rightToleft {
	-moz-animation: rightToleft 0.5s linear 0s 1 alternate;
	-webkit-animation: rightToleft 0.5s linear 0s 1 alternate;
	animation: rightToleft 0.5s linear 0s 1 alternate;
	-moz-animation-fill-mode: forwards;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
  }
  

  /*  */
  @-moz-keyframes leftToright {
	from {
	  transform: translateX(-100%);
	}
	to {
	  transform: translateX(0);
	}
  }
  @-webkit-keyframes leftToright {
	from {
	  transform: translateX(-100%);
	}
	to {
	  transform: translateX(0);
	}
  }
  @keyframes leftToright {
	from {
	  transform: translateX(-100%);
	}
	to {
	  transform: translateX(0);
	}
  }

  .leftToright {
	-moz-animation: leftToright 0.5s linear 0s 1 alternate;
	-webkit-animation: leftToright 0.5s linear 0s 1 alternate;
	animation: leftToright 0.5s linear 0s 1 alternate;
	-moz-animation-fill-mode: forwards;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
  }



</style>




